@import 'vtortola-terminal';
@import 'color-schemes';

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, blockquote,a,
abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav {
  display: block;
}

html {
  overflow: hidden;
}

body {
  line-height: 1;
  background-color: black;
  height: 100%;
}

ol, ul {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.cmdWindow {
  width: 100%;
  height: calc(100vh - 100px);
  /*height: 100vh;*/
  // background-color: black;
  // color: white;
  font-family: "Courier New", "Courier", monospace;
  margin: auto;
  text-indent: 15px;
  overflow-y:auto;
}

stats {
  visibility: hidden;
}

#stats {
  width: 100%;
  height: 50px;
  background-color: black;
  color: white;
  font-family: "Courier New", "Courier", monospace;
  /*margin: auto;*/
  margin-left: auto;
  margin-right: auto;
  padding-top:15px;
  display: table;
  
  p {
    text-align: center;
    display: table-cell;
    font-family: monospace;
    white-space: pre;
    margin: 1em 0;
  }
}

input:focus, select:focus, textarea:focus, button:focus {
  outline: none;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  
  &-button {
    width: 0px;
    height: 0px;
  }
  
  &-thumb {
    background: #4d4d4d;
    border: 0px none #ffffff;
    border-radius: 100px;
    
    &:hover {
      background: #6e6e6e;
    }
    
    &:active {
      background: #000000;
    }
  }
  
  &-track {
    background: #000000;
    border: 0px none #ffffff;
    border-radius: 100px;
    
    &:hover {
      background: #313131;
    }
    
    &:active {
      background: #4a4a4a;
    }
  }
  
  &-corner {
    background: transparent;
  }
}

.terminal {
  height: 100%;
  overflow-x: scroll;
}
